import '../App';

export default function HelpComponent(props){
    const componentBackgroundColor = props.color
    return( 
        <div className='info-container' style={{backgroundColor: props.color}}>
            <div>
                <h2>Instrukcja:</h2>
                <ol type="1">
                    <li>Wybierz model rozpoznawania z listy Wybierz model. Kady model daje inne rezultaty, zwykle najwysz skuteczno w rozpoznawaniu pisma gotyckiego ma domylnie wybrany frak2021_1.069.</li>
                    <li>Nacinij na pole Umie plik i wybierz obraz w formacie png, jpeg, jpg, tif. Moesz take przecign i upuci obraz w polu.</li>
                    <li>Proces transliteracji rozpocznie si automatycznie. Jego postp pokazuje pasek u dou ekranu. Szybko zaley od poczenia internetowego (musi zosta pobrany wybrany model, rozpoznawanie odbywa si lokalnie na komputerze uytkownika i aden obraz nie jest wysyany na serwer).</li>
                    <li>Po ukoczeniu procesu tekst wywietlany jest w polu na rodku ekranu, mona go poprawia za pomoc wbudowanego edytora. Na pasku narzdzi edytora znajduje si funkcja Popraw automatycznie, ktra poprawia bdnie rozpoznane znaki. Zmiana modelu powoduje ponowne przetworzenie obrazu. Na prawej stronie ekranu wywietlany jest podgld obrazu.</li>
                    <li>By transliterowa kolejny obraz, naley go doda w sposb opisany w punkcie 2.</li>
                </ol>
            </div>
            <div>
                <h2>Wymagania dotyczce obrazu:</h2>
                <ol>
                    <li>Najlepiej, gdy ma on min. 300-400 dpi.</li>
                    <li>Nie powinien mie zagi i innych utrudniajcych odczytanie skaz.</li>
                    <li>Nie powinien by zbinaryzowany, czyli czarnobiay (nieznacznie obnia to skuteczno).</li>
                </ol>
            </div>
            <div>
                <h2>O projekcie:</h2>
                <p>Aplikacja dziaa w oparciu o opensourcowy silnik Tesseract w wersji 5.1.0, a konkretnie jego JavaScriptowy port udostpniony w ramach projektu Naptha.
                Za podstaw do rozpoznawania obrazu posuyy modele dla drukw historycznych stworzone w Bibliotece Uniwersytetu w Mannheim, gdzie w ostatnim czasie, w ramach projektu OCR-BW, prowadzone s intensywne i rnorodne inicjatywy dotyczce automatyzacji rozpoznawania tekstu i jego transkrypcji. Modele te dostpne s przez bibliotek bezpatnie. Zostay one jednak, co nie jest zaskakujce, wytrenowane na historycznej niemieckiej frakturze oraz wczesnych drukach aciskich, w zwizku z czym nie rozpoznaj wielu polskich znakw diakrytycznych. Mog by jednak dobrym punktem startowym do trenowania modeli rozpoznajcych pismo rodzime.
                W obecnym stanie Incunae moe suy raczej jako ciekawostka, pokazujca, e moliwe jest automatyczne transkrybowanie nawet najstarszych polskich drukw. </p>
                <h2>Aplikacja powstaa przy uyciu:</h2>
                <ol>
                    <li><a href='https://pl.reactjs.org/' target={"blank"}>React</a></li>
                    <li><a href='https://tesseract.projectnaptha.com/' target={"blank"}>Tesseract.js</a></li>
                    <li><a href='https://quilljs.com/' target={"blank"}>Quill</a></li>
                    <li><a href='https://github.com/prc5/react-zoom-pan-pinch' target={"blank"}>React Zoom Pan Pinch</a></li>
                    <li><a href='https://create-react-app.dev/' target={"blank"}>Create React App</a></li>
                    <li><a href='https://reactcommunity.org/react-transition-group/' target={"blank"}>React Transition Group</a></li>
                    <li><a href='https://github.com/' target={"blank"}>Github Pages</a></li>
                </ol>
            </div>
            <div>
                <h2>Lista uytych modeli</h2>
                <ol>
                    <li><a href='https://github.com/tesseract-ocr/tesstrain/wiki/GT4HistOCR#frak2021' target={"blank"}>frak2021</a>  najdokadniejszy model sucy do rozpoznawania dawnej niemieckiej fraktury wypracowny w Bibliotece Uniwersytetu w Mannheim</li>
                    <li><a href='https://github.com/tesseract-ocr/tesstrain/wiki/GT4HistOCR#frak2021' target={"blank"}>Fraktur_5000000</a>  model sucy do rozpoznawania dawnej niemieckiej fraktury wypracowny w Bibliotece Uniwersytetu w Mannheim</li>
                    <li><a href='https://github.com/tesseract-ocr/tesstrain/wiki/GT4HistOCR#frak2021' target={"blank"}>GT4HistOCR</a>  model sucy do rozpoznawania dawnej niemieckiej fraktury oraz wczesnej aciny wypracowny w Bibliotece Uniwersytetu w Mannheim</li>
                    <li><a href='https://is.muni.cz/th/422162/fi_b/?lang=en' target={"blank"}>ces_frak</a>  model sucy do rozpoznawania czeskiej fraktury wypracowny w ramach pracy dyplomowej Martina Mejzlka</li>
                    <li><a href='https://tesseract-ocr.github.io/tessdoc/tess3/Data-Files.html' target={"blank"}>frk</a>  podstawowy model sucy do rozpoznawania dwudziestowiecznej niemieckiej fraktury</li>
                    <li><a href='https://tesseract-ocr.github.io/tessdoc/tess3/Data-Files.html' target={"blank"}>pol</a>  podstawowy model sucy do rozpoznawania wspczesnej polskiej antykwy, sprawdza si dobrze w rozpoznawaniu tekstw wydanych od poowy 18 wieku</li>
                </ol>
            </div>
        </div>
    )
}