import '../App';


export default function InfoComponent(props){
    return(
        <div className='info-container'>
            <h2>Incunae to program sucy do automatycznego transliterowania dawnych tekstw.</h2>
            <p>Dziki optycznemu rozpoznawaniu znakw (OCR) oraz modelom opartym na uczeniu maszynowym program transliteruje druki wydawane od XVI wieku.</p>
            <p>Niestety nie istniej jeszcze modele stworzone stricte dla polskich znakw, konieczne jest korzystanie z modeli niemieckich. W polu Wybierz model dostpne s cztery modele wytrenowane przez pracownikw Biblioteki Uniwersytetu w Mannheim. Maj one dobr skuteczno, jednak w zdecydowanej wikszoci nie rozpoznaj poprawnie znakw charakterystcznych dla dawnej polszczyzny.</p>
            <p>Sytuacj poprawia znajdujca si w edytorze funkcja znajd pochylone, ktra automatycznie zmienia nierozpoznane znaki, jadnak wci w wikszoci litery takie jak: , , ,  itd. nie s poprawnie wykrywane.</p>
            <p>By dowiedzie si wicej, <a onClick={props.help}>kliknij tu</a> lub nacinij na znak zapytania w prawym grnym rogu ekranu.</p>
        </div>
    )
}